export default (vue, selector, top = 0, left = 0) => {
	var $ = vue.$
	
	function update_position(vue, selector, top = 0, left = 0) {

		var elements = $(selector)

		elements.each( (index, el) => {
			
			if( $(el).parent().hasClass('extra-wrapper-fixit') === false )
			{
				$(el).wrap('<div class="extra-wrapper-fixit"></div>')
			}

			$(el).parent().css({
				'position': 'fixed',
				'top': $(vue.$el).position().top + 'px',
				'width':  $(vue.$el).innerWidth(),
				'height': $(vue.$el).height(),
				'overflow': 'hidden',
				'pointer-events': 'none',
			});

			//console.log( 'pos1', $(vue.$el).position().top + $('#app').scrollTop() )
			//console.log( 'pos2', $(vue.$el).position().top * -1 )

			$(el).css({
				'position': 'absolute',
				'top': $(vue.$el).position().top * -1,
				'left': 0,
				'margin-top': top,
				'margin-left': left,
				'pointer-events': 'auto',
			});	
		});
		
	}
	
	update_position(vue, selector, top, left)

	$('#app').on('scroll', () => {
		update_position(vue, selector, top, left)
	});
}